import conf from "./config";

const loginUser = (postData) => {
  return fetch(conf.base_api + "users/signin", {
    method: "POST",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
    body: JSON.stringify(postData),
  })
  .then((res) => {
    return res.json();
  })
  .catch((error) => {
    return Promise.reject(error);
  });
};

